@import url("https://fonts.googleapis.com/css2?family=Lato&display=swap");
.text {
  font-size: 1.2rem;
  color: aliceblue;
  transition: all 1.3s ease-in-out;
  cursor: pointer;
  font-family: "Lato", sans-serif;
}

.row {
  display: flex;
  justify-content: space-between;
}
.item {
  margin: 1rem;
  padding: 0 1rem;
  display: flex;
  font-size: 1.2rem;
  color: aliceblue;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  border-radius: 2rem;
}
.item:hover {
  transform: scale(1.1);
  background-color: rgba(0, 0, 0, 0.3);
}

.closeIconBox {
  display: flex;
  justify-content: flex-end;
}

.closeIcon {
  color: aliceblue;
  cursor: pointer;
  transition: all 0.5s ease-in-out;
}

.closeIcon:hover {
  transform: scale(1.2);
  background-color: rgba(0, 0, 0, 0.3);
}
