.modal {
  position: fixed;
  top: 10vh;
  left: 35%;
  width: 30%;
  height: auto;
  background: rgb(2, 8, 59);
  background: linear-gradient(
    90deg,
    rgba(2, 8, 59, 1) 35%,
    rgba(15, 91, 159, 1) 98%
  );
  padding: 1rem;
  border-radius: 14px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  z-index: 30;
  animation-name: showModal;
  animation-timing-function: ease-in-out;
  animation-duration: 0.3s;
}

@keyframes showModal {
  0% {
    transform: translateY(-50%);
    opacity: 0;
  }

  100% {
    transform: translateY(0%);
    opacity: 1;
  }
}
